html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
}

body, p, h3 {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;
  padding: 32px 16px 16px;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.product-card__product-code {
  color: #616070;
  margin-bottom: 16px;
}

.product-card__reviews-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product-card__price-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.product-card__price-text {
  color: #616070;
  font-size: 12px;
}

.product-card__button {
  text-transform: uppercase;
  color: #fff;
  box-sizing: border-box;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

